import axios,{ uploadFile} from './http';
import base from './base';
import QS from 'qs'

let apis={}

//用户登录
apis.login=(params)=>{ return axios.post(`${base.url}/auth/login`,QS.stringify(params))};
//获取手机验证码
apis.code=(params)=>{ return axios.post(`${base.url}/auth/code`,QS.stringify(params))};
//验证用户是否已存在
apis.check=(params)=>{ return axios.get(`${ base.url}/auth/check`,{ params:params})};
//用户注册
apis.register=(params)=>{ return axios.post(`${ base.url}/auth/register`,QS.stringify(params))};
//用户修改密码
apis.editpass=(params1,params2)=>{ return axios.put(`${base.url}/student/user/pass/${params1}/${params2}`)};
//手机号认证
apis.userphone=(params1,params2)=>{ return axios.post(`${ base.url}/student/user/phone/${params1}/${params2}`)};
//获取邮箱验证码
apis.getmailcode=(params)=>{ return axios.get(`${ base.url}/student/user/mail/${params}`)};
//邮箱认证
apis.mailcode=(params1,params2)=>{ return axios.put(`${ base.url}/student/user/mail/${params1}/${params2}`)};
//修改昵称
apis.editnick=(params)=>{ return axios.put(`${ base.url}/student/user/nick`,QS.stringify(params))};
//获得用户信息
apis.userinfo=(params)=>{ return axios.get(`${base.url}/student/user`,{params:params})}
//手机找回密码
apis.findpasstel=(params)=>{ return axios.get(`${ base.url}/auth/findPass/tel/${params}`)}
apis.findtelcode=(params1,params2)=>{ return axios.post(`${ base.url}/auth/findPass/tel/${params1}`,QS.stringify(params2))}
//邮箱找回密码
apis.findpassem=(params)=>{ return axios.get(`${base.url}/auth/findPass/mail/${params}`)}
apis.findemcode=(params1,params2)=>{ return axios.post(`${ base.url}/auth/findPass/mail/${params1}`,QS.stringify(params2))}

//当前用户考生信息
apis.studentinfo=(params)=>{ return axios.get(`${base.url}/student/info`,{ params:params})};
//用户首次注册考生信息
apis.testinfo=(params)=>{ return axios.post(`${ base.url}/student/info`,QS.stringify(params))};
apis.upImg=(params)=>{ return uploadFile(`${ base.url}/student/info/upload`,params)}

//查看已发布的考试信息
apis.stuexam=(params)=>{ return axios.get(`${base.url}/student/exam`,{params:params})}
//考生报名
apis.signexam=(params1,params2)=>{ return axios.post(`${ base.url}/student/sign/${params1}/${params2}`)}
//考生已报考信息
apis.stusign=(params)=>{ return axios.get(`${base.url}/student/sign`,{params:params})}

//考生查询成绩
apis.stugrade=(params)=>{ return axios.get(`${ base.url}/student/grade`,{ params:params})}
//打印准考证
apis.download=(params)=>{ return axios.get(`${ base.url}/public/admission/download`,{ params:params})};
//下拉获取考次
apis.examlist=(params)=>{ return axios.get(`${ base.url}/public/exam/list`,{ params:params})};

//成绩查询
apis.querygrade=(params)=>{ return axios.post(`${base.url}/public/score`,QS.stringify(params))};

//缴费
apis.putpay=(params)=>{ return axios.post(`${base.url}/student/sign/pay/${params}`)};

apis.addr=(params)=>{ return axios.get(`${base.url}/student/info/addr`,{params:params})}

export default apis;